@media screen and (max-width : 700px)
{
    #topArtistsDiv {
        position: absolute;
        opacity: 1;
        margin: 0;
        bottom: -700px;
        left: 50%;
        -ms-transform: translate(-50%);
        transform: translate(-50%);
        /* background-color: rgb(3, 3, 29, 1); */
        background-color: rgb(32, 32, 32);
        border-radius: 25px;
        padding: 20px;
        width: 90%;
        height: auto;
        color: white;
        transition: 1s;
        z-index: 1;
        align-items: center;
    }
}
@media screen and (min-width : 700px) and (max-width : 10000px)
{
    #topArtistsDiv {
        position: absolute;
        opacity: 1;
        margin: 0;
        bottom: -700px;
        left: 50%;
        -ms-transform: translate(-50%);
        transform: translate(-50%);
        /* background-color: rgb(3, 3, 29, 1); */
        background-color: rgb(32, 32, 32);
        border-radius: 25px;
        padding: 20px;
        width: 600px;
        height: 350px;
        color: white;
        transition: 1s;
        z-index: 1;
        align-items: center;
    }
}

#topArtistsButton {
    position: absolute;
    margin: 0;
    bottom: 5%;
    left: 2.5%;
    border: none;
    outline: none;
    cursor: pointer;
    color: white;
    opacity: 1;
}

.first-icon {
    width: 30px;
    height: 30px;
}

.slide-up-bottom {
    background: blue;
    -webkit-animation: slide-up-bottom 0.5s forwards;
    animation: slide-up-bottom 0.5s forwards;
}
@-webkit-keyframes slide-up-bottom {
    0% { bottom: -700px }
    100% { bottom: -150px; }
}
@keyframes slide-up-bottom {
    0% { bottom: -700px }
    100% { bottom: -150px; }
}

.slide-down-bottom {
    background: blue;
    -webkit-animation: slide-down-bottom 0.5s backwards;
    animation: slide-down-bottom 0.5s backwards;
}
@-webkit-keyframes slide-down-bottom {
    0% { bottom: -150px }
    100% { bottom: -700px; }
}
@keyframes slide-down-bottom {
    0% { bottom: -150px }
    100% { bottom: -700px; }
} 